import React from 'react';

import { makeStyles } from '@material-ui/core/styles';

import
{
    Button,
    CssBaseline,
    TextField,
    FormControlLabel,
    Checkbox,
    Link,
    Grid,
    Box,
    Typography,
    Container,
    InputAdornment,
    IconButton,
} from "@material-ui/core"

import
{
    AccountCircle,
    VpnKey,
    Visibility,
    VisibilityOff,
} from "@material-ui/icons"

import Defines from "./Defines";

function Copyright()
{
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://material-ui.com/">
                TeamChicken.de
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

class SignInPage extends React.Component
{
    classes = makeStyles(theme => ({
        paper: {
            marginTop: theme.spacing(8),
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
        },
        // form: {
        //     width: '100%', // Fix IE 11 issue.
        //     marginTop: theme.spacing(1),
        //     backgroundColor: '#ffffff',
        // },
        // submit: {
        //     margin: theme.spacing(1, 0, 2),
        //     backgroundColor: '#2196f3',
        //     border: '1px solid',
        //     borderColor: '#000000',
        //     color: '#ffffff',
        // },
        // tf1: {
        //     margin: theme.spacing(1, 0, 2),
        //     backgroundColor: '#ffffff',
        //
        // }
    }));

    constructor(props)
    {
        super(props);

        this.state = {
            email: undefined,
            password: undefined,
            errorActive: false,
            showPassword: true
        };
    }


    xxx = () =>
    {
        console.debug("submit button pressed");
        console.debug(this.state.password);
        console.debug(this.state.email);

        if (this.state.email !== "" && this.state.password !== "")
        {
            console.debug("nicht leer")
            //todo alert bitte alle felder ausfüllen

            // https://stackoverflow.com/questions/33840150/onclick-doesnt-render-new-react-component
        }
        
        var POSTJSON = {
            email: this.state.email,
            password: this.state.password
        }

        //creates JSONARRAY of email and pw

        fetch(Defines.server() + '/userDataCheck', {
            method: 'POST',
            body: JSON.stringify(POSTJSON),
        })
            .then(response => response.json())
            .then(data =>
            {
                console.debug(this.LOGTAG, "Sign: ", data.success)
                this.serverResponse(data);
            })
            .catch(error =>
            {
                console.error(error)
            });

    };

    serverResponse = (data) =>
    {
        if (!data["success"])
        {
            this.setState({
                errorActive: true
            });

            return;
        }

        console.debug("anmeldedaten richtig");
        window.location.href = window.location.origin + window.location.pathname + "?page=dashboard&user=" + data["userId"];

        console.debug("errorActive== " + this.state.errorActive);
    }


    onPasswordChanged = (event) =>
    {
        console.debug("onPasswordChanged event", event);
        console.debug("onPasswordChanged value", event.target.value);

        this.setState({
            password: event.target.value
        });
    };

    onEmailChanged = (event) =>
    {
        console.debug("onEmailChanged event", event);
        console.debug("onEmailChanged value", event.target.value);

        this.setState({
            email: event.target.value
        });
    };

    SignUpRouting = (event) =>
    {
        window.location.href = window.location.origin + window.location.pathname + "?page=SignUp";
    }
    handleClickShowPassword = () =>
    {
        this.setState({
            showPassword: !this.state.showPassword
        });
    };

    handleMouseDownPassword = (event) =>
    {
        event.preventDefault();
    };

    render()
    {
        let isErrPw = false;
        if (this.state.password === "")
        {
            isErrPw = true;
        }

        let errIsEmailAnEmail = true;
        if ((this.state.email) && this.state.email.match(/^[A-Za-z0-9.-_]+@[A-Za-z0-9.-_]+\.[a-z]+$/))
        {
            errIsEmailAnEmail = false;
        }
        if (this.state.email === undefined)
            errIsEmailAnEmail = false


        console.debug("REGEX: is email an email? " + errIsEmailAnEmail);
        console.debug("this.state.email= '" + this.state.email + "'");
        console.debug("this.state.password= '" + this.state.password + "'");
        console.debug("isErrPw= " + isErrPw);

        //todo @mori bild align center
        return (
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <div className={this.classes.paper}>

                    <center>
                        <img src="data:image/png;base64,"
                            width="45%"
                            alt="alternativer_text" />
                    </center>

                    <div>
                        {this.state.errorActive ? (
                            <Typography component="h1" variant="h5">
                                <span style={{ color: "#ff0000" }}>WRONG LOGIN!</span>
                            </Typography>
                        ) : (
                                <div></div>
                            )}
                    </div>
                    <form className={this.classes.form} noValidate>
                        <TextField
                            className={this.classes.tf1}
                            InputProps={{
                                disableUnderline: true, startAdornment: (
                                    <InputAdornment position='start'>
                                        <AccountCircle />
                                    </InputAdornment>
                                ),
                            }}
                            variant="outlined"
                            required
                            fullWidth
                            id="email"
                            label="Email Addresse"
                            name="email"
                            autoComplete="email"
                            autoFocus
                            dafaultvalue={this.state.email}
                            onChange={this.onEmailChanged}
                            helperText={errIsEmailAnEmail ? 'Not an Email!' : ' '}
                            error={errIsEmailAnEmail}
                        />
                        <TextField
                            InputProps={{
                                disableUnderline: true, startAdornment: (
                                    <InputAdornment position='start'>
                                        <VpnKey />
                                    </InputAdornment>
                                ),
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton
                                            aria-label="toggle password visibility"
                                            onClick={this.handleClickShowPassword}
                                            onMouseDown={this.handleMouseDownPassword}
                                            edge="end"
                                        >
                                            {/*<Delete />*/}
                                            {this.state.showPassword ? <VisibilityOff /> : <Visibility />}
                                        </IconButton>
                                    </InputAdornment>
                                )
                            }}

                            className={this.classes.tf1}
                            variant="outlined"

                            required
                            fullWidth
                            name="password"
                            label="Passwort"
                            type={this.state.showPassword ? "password" : undefined}
                            id="password"
                            autoComplete="current-password"
                            defaultValue={this.state.password}
                            onChange={this.onPasswordChanged}
                            helperText={isErrPw ? 'Empty field!' : ' '}
                            error={isErrPw}


                        />
                        <FormControlLabel
                            control={<Checkbox value="remember" color="primary" />}
                            label="Erinnere mich"
                        />
                        <Button
                            //type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            id="Submit"
                            className={this.classes.submit}
                            onClick={this.xxx}
                        >
                            Anmelden
                        </Button>
                        <Grid container>
                            <Grid item xs>
                                <Link href="/404" variant="body2">
                                    Passwort vergessen?
                                </Link>
                            </Grid>
                            <Grid item>
                                <Link onClick={this.SignUpRouting} variant="body2"
                                //onClick={window.location.href ='/'}
                                >
                                    {"Noch kein Account? Hier regestrieren"}
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                </div>
                <Box mt={8}>
                    <Copyright />
                </Box>
            </Container>

        );
    }
}

export default SignInPage;
