import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import
{
    Button,
    CssBaseline,
    TextField,
    FormControlLabel,
    Checkbox,
    Link,
    Grid,
    Box,
    Typography,
    Container,
    InputAdornment,
    IconButton,
} from "@material-ui/core"

import
{
    AccountCircle,
    VpnKey,
    Visibility,
    VisibilityOff,
} from "@material-ui/icons"

import Defines from "./Defines";

function Copyright()
{
    return (
        <Typography variant="body2" color="textSecondary" align="center">
            {'Copyright © '}
            <Link color="inherit" href="https://material-ui.com/">
                Your Website
            </Link>{' '}
            {new Date().getFullYear()}
            {'.'}
        </Typography>
    );
}

class SignUpPage extends React.Component
{
    classes = makeStyles(theme => ({
        paper: {
            marginTop: theme.spacing(8),
            display: 'flex',
            flexDirection: 'column',
            alignItems: 'center',
        },
        avatar: {
            margin: theme.spacing(1),
            backgroundColor: theme.palette.secondary.main,
        },
        form: {
            width: '100%', // Fix IE 11 issue.
            marginTop: theme.spacing(3),
        },
        submit: {
            margin: theme.spacing(3, 0, 2),
        },
    }));

    constructor(props)
    {
        super(props);

        this.state = {
            firstName: undefined,
            secondName: undefined,
            email: undefined,
            password: undefined,
            routing: false
        };
    }

    onFNameChanged = (event) =>
    {
        this.setState({
            firstName: event.target.value
        });
    };

    onSNameChanged = (event) =>
    {
        this.setState({
            secondName: event.target.value
        });
    };

    onEmailChanged = (event) =>
    {
        this.setState({
            email: event.target.value
        })
    }


    handleClickShowPassword = () =>
    {
        this.setState({
            showPassword: !this.state.showPassword
        });
    };


    onPasswordChanged = (event) =>
    {
        this.setState({ password: event.target.value })
    }

    SignInRouting = (event) =>
    {
        window.location.href = window.location.origin + window.location.pathname + "?page=SignIn";
    }

    safeData = () =>
    {

        var postJSON = {
            "name": this.state.firstName,
            "lastname": this.state.secondName,
            "email": this.state.email,
            "password": this.state.password,
        }
        console.debug(postJSON)

        // postJSON[ "key" ] = "value";

        //todo @k
        //siehe editing page: stringifizieren und dann an server

        if ((this.state.firstName  === "" || this.state.firstName  === undefined) &&
            (this.state.secondName === "" || this.state.secondName === undefined) &&
            (this.state.email      === "" || this.state.email      === undefined) &&
            (this.state.password   === "" || this.state.password   === undefined))
        {
            console.debug("Data Empty");
            window.alert("Fields Empty")
        }
        else
        {

            // fetch(defines.server() + '/userData/' + this.state.uuid, {
            fetch(Defines.server() + '/userData', {
                method: 'POST',
                body: JSON.stringify(postJSON)
            })
                .then(response => response.json())
                .then(data =>
                {
                    console.debug("SignUP: ", data)
                    if (!data.success)
                    {
                        window.alert("Userdata already exist!");
                    }
                    else
                    {
                        this.SignInRouting();
                    }
                })
                .catch(error =>
                {
                    console.error(this.LOGTAG, "onEditorChange", error)
                });
            this.setState({ routing: true });

        }
    }

    render()
    {
        let isFNameErr = false;

        if (this.state.firstName === undefined)
        {
            isFNameErr = false;
        }

        if (this.state.firstName === "")
            isFNameErr = true;


        let isSNameErr = false;

        if (this.state.secondName === undefined)
            isSNameErr = false;

        if (this.state.secondName === "")
            isSNameErr = true;

        let errIsEmailAnEmail = true;
        if ((this.state.email) && this.state.email.match(/^[A-Za-z0-9.-_]+@[A-Za-z0-9.-_]+\.[a-z]+$/))
        {
            errIsEmailAnEmail = false;
        }
        if (this.state.email === undefined)
            errIsEmailAnEmail = false

        let isPWErr = false;
        if (this.state.password === undefined)
            isPWErr = false

        if (this.state.password === "")
            isPWErr = true


        return (
            <Container component="main" maxWidth="xs">
                <CssBaseline />
                <div className={this.classes.paper}>
                    <center>
                        <img src="data:image/png;base64,"
                            width="45%"
                            alt="alternativer_text" />
                    </center>

                    <form className={this.classes.form} noValidate>
                        <Grid container spacing={2}>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    autoComplete="fname"
                                    name="firstName"
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="firstName"
                                    label="Vorname"
                                    autoFocus
                                    value={this.state.firstName}
                                    error={isFNameErr}
                                    onChange={this.onFNameChanged}
                                    helperText={isFNameErr ? 'Empty field!' : ''}
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="lastName"
                                    label="Nachname"
                                    name="lastName"
                                    autoComplete="lname"
                                    onChange={this.onSNameChanged}
                                    error={isSNameErr}
                                    helperText={isSNameErr ? 'Empty field!' : ''}
                                    value={this.state.secondName}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    className={this.classes.tf1}
                                    InputProps={{
                                        disableUnderline: true, startAdornment: (
                                            <InputAdornment position='start'>
                                                <AccountCircle />
                                            </InputAdornment>
                                        ),
                                    }}
                                    variant="outlined"
                                    required
                                    fullWidth
                                    id="email"
                                    label="Email Addresse"
                                    name="email"
                                    autoComplete="email"
                                    autoFocus
                                    value={this.state.email}
                                    onChange={this.onEmailChanged}
                                    error={errIsEmailAnEmail}
                                    helperText={errIsEmailAnEmail ? 'Not a e-mail address!' : ''}

                                />
                            </Grid>
                            <Grid item xs={12}>
                                <TextField
                                    InputProps={{
                                        disableUnderline: true, startAdornment: (
                                            <InputAdornment position='start'>
                                                <VpnKey />
                                            </InputAdornment>
                                        ),
                                        endAdornment: (
                                            <InputAdornment position="end">
                                                <IconButton
                                                    aria-label="toggle password visibility"
                                                    onClick={this.handleClickShowPassword}
                                                    onMouseDown={this.handleMouseDownPassword}
                                                    edge="end"
                                                >
                                                    {/*<Delete />*/}
                                                    {this.state.showPassword ? <VisibilityOff /> : <Visibility />}
                                                </IconButton>
                                            </InputAdornment>
                                        )
                                    }}

                                    className={this.classes.tf1}
                                    variant="outlined"

                                    required
                                    fullWidth
                                    name="password"
                                    label="Passwort"
                                    type={this.state.showPassword ? "password" : undefined}
                                    id="password"
                                    autoComplete="current-password"
                                    defaultValue={this.state.password}
                                    onChange={this.onPasswordChanged}
                                    error={isPWErr}
                                    helperText={isPWErr ? 'No password!' : ''}
                                />
                            </Grid>
                            <Grid item xs={12}>
                                <FormControlLabel
                                    control={<Checkbox value="allowExtraEmails" color="primary" />}
                                    label="Ich akzeptiere die AGBs und Datenschutzerklärungen"
                                />
                            </Grid>
                        </Grid>
                        <Button
                            fullWidth
                            variant="contained"
                            color="primary"
                            className={this.classes.submit}
                            onClick={() =>
                            {
                                this.safeData()
                                //window.location.href = "/SignIn"
                                // if (this.state.routing === true)
                                // {this.SignInRouting()}
                            }}
                        >
                            Registrieren
                        </Button>
                        <Grid container justify="flex-end">
                            <Grid item>
                                <Link onClick={this.SignInRouting} variant="body2">
                                    Bereits regestriert? Hier anmelden
                                </Link>
                            </Grid>
                        </Grid>
                    </form>
                </div>
                <Box mt={5}>
                    <Copyright />
                </Box>
            </Container>
        );
    }
}

export default SignUpPage;
