import React from 'react';
import { makeStyles } from '@material-ui/core/styles';

import StarIcon from '@material-ui/icons/StarBorder';

import
{
    AppBar,
    Button,
    Card,
    CardActions,
    CardContent,
    CardHeader,
    CssBaseline,
    Grid,
    Toolbar,
    Typography,
    Container,
} from "@material-ui/core"

class WelcomePage extends React.Component
{

    SignUpRouting = () =>
    {
        window.location.href = window.location.origin + window.location.pathname + "?page=SignUp";
    }

    SignInRouting = () =>
    {
        window.location.href = window.location.origin + window.location.pathname + "?page=SignIn";
    }

    classes = makeStyles(theme => ({
        '@global': {
            ul: {
                margin: 0,
                padding: 0,
            },
            li: {
                listStyle: 'none',
            },
        },
        appBar: {
            borderBottom: `1px solid ${theme.palette.divider}`,
        },
        toolbar: {
            flexWrap: 'wrap',
        },
        toolbarTitle: {
            flexGrow: 1,
        },
        link: {
            margin: theme.spacing(1, 1.5),
            position: 'right',
        },
        heroContent: {
            padding: theme.spacing(8, 0, 6),
        },
        cardHeader: {
            backgroundColor:
                theme.palette.type === 'dark' ? theme.palette.grey[700] : theme.palette.grey[200],
        },
        cardPricing: {
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'baseline',
            marginBottom: theme.spacing(2),
        },
    }));

    //const
    tiers = [
        {
            title: 'Free-Trial',
            subheader: 'zum Ausprobieren',
            price: '0',
            description: ['30 Testminuten',
                'Help Center Zugang',
                'Einverständnis der Weitervervendung und Weitergabe an Dritte ',
                'Gut geeignet zum Testen unseres Services'
            ],
            buttonText: 'Kostenlos Registrieren',
            buttonVariant: 'outlined',

        },
        {
            title: 'Standard',
            price: '20',
            subheader: 'am meisten genutzt',
            description: [
                'Unbegrenzte Transkriptionen',
                '5 GB Speicher',
                'Help Center Zugang',
                'Email Support',
                'Download der Transkription als .txt oder als .json',
                'Schwärzen, Hotkeys, Sprecherwechsel und weitere Funktionen',
                'Weiterverwendung der Daten zum Verbessern unseres Services'
            ],
            buttonText: 'Beginnen sie jetzt',
            buttonVariant: 'contained',
        },
        {
            title: 'Pro',
            price: '30',
            subheader: 'für vertrauliche Daten',
            description: [
                'Unbegrenzte Transkriptionen',
                '20 GB Speicher',
                'Help Center Zugang',
                'Premium Email Support',
                'Download der Transkription als .txt oder als .json',
                'Schwärzen, Hotkeys, Sprecherwechsel und weitere Funktionen',
                'Keine Weiterverwendung der Daten'
            ],
            buttonText: 'Kontaktieren sie uns',
            buttonVariant: 'outlined',
        },
    ];


    // const classes = useStyles();
    render()
    {
        var offers = (
            <Container maxWidth="lg" component="main">
                <Grid
                    container
                    spacing={7}>
                    {this.tiers.map(tier => (
                        // Enterprise card is full width at sm breakpoint
                        <Grid
                            item key={tier.title}
                            xs={6}
                            sm={tier.title === 'Enterprise' ? 12 : 6}
                            md={4}>
                            <Card>
                                <CardHeader
                                    title={tier.title}
                                    subheader={tier.subheader}
                                    titleTypographyProps={{ align: 'center' }}
                                    subheaderTypographyProps={{ align: 'center' }}
                                    action={tier.title === 'Standard' ? <StarIcon /> : null}
                                    className={this.classes.cardHeader}
                                />
                                <CardContent>
                                    <div className={this.classes.cardPricing}>
                                        <Typography component="h2" variant="h3" color="textPrimary">
                                            €{tier.price} <span variant="h6" style={{ color: "grey" }}>/h</span>
                                        </Typography>
                                    </div>
                                    <div style={{ height: "275px", overflow: "scroll" }}>
                                        <ul>
                                            {tier.description.map(line => (
                                                <Typography component="li" variant="subtitle1" align="center"
                                                    key={line}>
                                                    {line}
                                                </Typography>
                                            ))}
                                        </ul>
                                    </div>
                                </CardContent>
                                <CardActions>
                                    <Button fullWidth variant={tier.buttonVariant} color="primary" onClick={this.SignUpRouting}>
                                        {tier.buttonText}
                                    </Button>
                                </CardActions>
                            </Card>
                        </Grid>
                    ))}
                </Grid>
            </Container>
        );

        return (
            <React.Fragment>
                <CssBaseline />

                <AppBar position="static" color="default" elevation={0} className={this.classes.appBar}>
                    <Toolbar className={this.classes.toolbar}>
                        <Typography variant="h6" color="inherit" noWrap className={this.classes.toolbarTitle}>
                            Team Chicken
                        </Typography>
                        <nav>

                        </nav>


                        <Button onClick={this.SignInRouting} color="primary" variant="outlined"
                            className={this.classes.link} style={{
                                position: "absolute",
                                right: "1%",
                            }}>
                            Login
                        </Button>
                    </Toolbar>
                </AppBar>
                {/* Hero unit */}
                <Container maxWidth="sm" component="main" className={this.classes.heroContent}>
                    <center>
                        <img src="data:image/png;base64,"
                            width="45%"
                            alt="alternativer_text" />
                    </center>
                    <Typography component="h1" variant="h2" align="center" color="textPrimary" gutterBottom>
                        Preise
                    </Typography>

                    <Typography variant="h6" align="center" color="textSecondary" component="p">
                        Wählen Sie das Angebot, das zu Ihnen passt!</Typography>
                    <p></p>
                </Container>
                {/* End hero unit */}

                {offers}

                {/* End footer */}
            </React.Fragment>
        );
    }
}

export default WelcomePage;
